.chat-window {
  padding-bottom: 28px;
  width: 500px;
  height: 100%;
  display: flex;
  flex-direction: column;
  .header {
    flex-shrink: 0;
  }
  .msg-list-wrap {
    padding: 20px 0;
    flex-grow: 1;
    overflow-y: auto;
    border-radius: 4px;
    background-color: #f7f9fd;
  }
  .msg-item {
    padding: 10px 20px;
  }
  .msg-item-content {
    padding: 8px;
    border-radius: 4px;
    font-size: 14px;
    p {
      margin-top: 8px;
      margin-bottom: 8px;
    }
  }
  .msg-item-user {
    text-align: right;
    .msg-item-content {
      background-color: rgb(245, 240, 244);
    }
  }
  .msg-item-model {
    .msg-item-content {
      background-color: rgb(245, 242, 240);
    }
  }
  .ant-input {
    flex-shrink: 0;
    margin-top: 20px;
    height: 100px;
  }
}